@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap");

:root {
    --bg-color: #282c34;
    --text-color: #ccc;
    --big-font: 5rem;
    --h2-font: 3rem;
    --p-font: 1.6rem;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;

    text-decoration: none;
    scroll-behavior: smooth;
}

.app {
    background: var(--bg-color);
    margin-top: -2rem;
    padding-top: 2rem;
}

.app header {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    margin: 0 8rem;
    color: var(--text-color);
}

.app header .sub-header {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;

    align-items: center;
}

.app h1 {
    color: var(--text-color);
    font-size: var(--big-font);
}

.app h2 {
    font-size: var(--h2-font);
}

.app header h1,
h2,
ul {
    margin: 1.6rem 0;
    color: var(--text-color);
}
.app header p,
li {
    font-size: var(--p-font);
}
.app > h1,
.section__pagination {
    margin: 2.5rem;
    color: var(--text-color);
}

.app > h2 {
    margin: 0 2.5rem;
    font-size: var(--h2-font);
}

.app .section__pagination {
    font-size: 2.5rem;

    cursor: pointer;
}

.app .section__pagination span {
    padding: 1.5rem 2rem;
    border: 0.1rem solid #ccc;
    margin: 0 0.2rem;
    border-radius: 0.8rem;
    transition: all 0.45s ease;
}

.app .section__pagination .pagination__selected {
    background-color: #ccc;
    color: var(--bg-color);
}

.app .section__pagination span:hover,
i:hover {
    background-color: #ccc;
    color: var(--bg-color);
}

.app .section__pagination .pagination__disabled {
    display: none;
}

.app .products {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    margin: 2rem;
    gap: 2rem;
}

.app .products .product__single {
    height: 17.5rem;
    padding: 2.5rem;
    background-color: #fff;
    text-align: center;
    cursor: pointer;
    border-radius: 0.8rem;
    font-size: 1.4rem;
}

.app .products .product__single > img {
    width: 100%;
    height: 95%;
    object-fit: cover;
    margin-bottom: 0.5rem;
    border-radius: 0.5rem;
}

.app .products .product__single .product__single_title {
    margin-bottom: 2.8rem;
}

/* media queries  */

@media (max-width: 1100px) {
    :root {
        --big-font: 4rem;
        --h2-font: 2.5rem;
        --p-font: 1.4rem;
        transition: 1s;
    }
}

@media (max-width: 920px) {
    :root {
        --big-font: 3rem;
        --h2-font: 2rem;
        --p-font: 1.2rem;
        transition: 1s;
    }
    .app .products {
        grid-template-columns: repeat(2, 1fr);
    }
    .app .products .product__single {
        height: 25.5rem;
    }
}

@media (max-width: 760px) {
    .app header {
        margin: 0 4rem;
    }
    .app header .sub-header {
        grid-template-columns: 1fr;
        gap: 0.5rem;
    }
    .app .section__pagination {
        font-size: 1rem;
    }

    .app .section__pagination span {
        padding: 0.45rem 0.7rem;
    }

    .app .products {
        grid-template-columns: 1fr;
    }
    .app .products .product__single {
        height: 40.5rem;
    }
}
